
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Previewer from 'vux/src/components/previewer/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

const pulldownDefaultConfig = {
content: "下拉刷新",
height: 40,
autoRefresh: false,
downContent: "下拉刷新",
upContent: "释放后刷新",
loadingContent: "正在刷新...",
clsPrefix: "xs-plugin-pulldown-"
};
const pullupDefaultConfig = {
content: "上拉加载更多",
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: "释放后加载",
upContent: "上拉加载更多",
loadingContent: "加载中...",
clsPrefix: "xs-plugin-pullup-"
};
export default {
components: {
Flexbox,
FlexboxItem,
AlertModule,
Previewer,
Scroller
},
data() {
return {
nowPage: 1,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
circleList: [],
uid:this.$route.query.uid,
loginUid: this.$store.state.uid,
postData: {},
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {
x: rect.left,
y: rect.top + pageYScroll,
w: rect.width
}


}
},
}
},
mounted() {
var that=this;
that.circleList=[]


that.$nextTick(() => {
that.$refs.scrollerBottom.reset({
top: 0
});
});
that.loadMore();
},
methods: {

refresh() {
var that = this;
if (that.nowPage != 0) {
that.nowPage--;
that.getCircleLoist(data => {
that.circleList = that.circleList.concat(data);
that.$refs.scrollerBottom.enablePullup();
that.$refs.scrollerBottom.donePulldown();
});
}
},
loadMore() {
var that = this;
that.getCircleLoist(data => {
if(data.length>0){
that.circleList = that.circleList.concat(data)
}
if (data.length >= 10) {
that.$refs.scrollerBottom.disablePullup();
}

that.$refs.scrollerBottom.donePullup();


});

that.nowPage++;

},


show(index,j) {
console.log('---'+index)
console.log(this.$refs.previewer)
this.$refs.previewer[index].show(j)
},

toCollection(id) {
var that = this;
var postData = {
uid: this.loginUid,
circleId: id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Collection', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.circleList=[]
that.nowPage=1;
that.loadMore()
}
})
},

toComment(id) {
alert(id)
this.$router.push({
path:'/circleDetail',
query:{
circleId:id
}
})
},

toLike(id) {
var that = this;
var postData = {
uid: this.loginUid,
circleId: id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Like', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.circleList=[]
that.nowPage=1;
that.loadMore()

}
})
},

toComment(id) {
this.$router.push({
path:'/circleDetail',
query:{
circleId:id
}
})
},


getCircleLoist(fn) {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.postData.currentPage = that.nowPage;
that.postData.loginUid = that.loginUid;
that.postData.uid = that.uid
that.$http('get', baseUrl + 'api/Circle/Page', that.postData).then(function (res) {
var result = res.data.data;

for (var i in result) {
if (result[i].imgurls != '') {

if (result[i].imgurls.indexOf(',') == -1) {

var Arr = []
var obj = {
src: that.$store.state.imgUrl + result[i].imgurls
}
Arr.push(obj)
result[i].imgurls = Arr;
} else {
result[i].imgurls = result[i].imgurls.split(',')
var arr2 = []
for (var k in result[i].imgurls) {
var obj = {};
obj.src = that.$store.state.imgUrl + result[i].imgurls[k];
arr2.push(obj)
}
result[i].imgurls = arr2;
}
}
}


fn(res.data.data)
})
},

toFellow(i) {
var that = this;
var postData = {
uid: this.uid,
followingUid: this.circleList[i].uid
}
that.$http('post', that.$store.state.baseUrl + 'api/UserFollowing', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.nowPage=1;
that.loadMore()

}
})
this.circleList[i].follow = !this.circleList[i].follow;
}
}
}



