

import common from "@/assets/js/common";
import axios from 'axios'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
XTextarea,
AlertModule
},
data() {
return {
content: "",
uid: this.$store.state.uid,
content: "",
chooseSrc: [],
imgVal: '',
imgArr: [],
showImgArr: [],
isios: false
};
},
mounted() {

var ua = navigator.userAgent.toLowerCase();

var isIos = (ua.indexOf('iphone') != -1) || (ua.indexOf('ipad') != -1);

if (isIos) {
this.isios = true;
} else {
this.isios = false
}
},
methods: {
...common,
clickChoose() {
var file = document.getElementById("upload_file");
file.click()
},

chooseImage() {
console.log(this.imgArr.length)
var that = this;
if (that.imgArr.length <= 8) {
var file = document.getElementById("upload_file").files[0];


lrz(file, {
width: 480
}).then(function (rst) {
var formdata1 = new FormData(); 
formdata1.append('img', file); 

let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头
axios.post(that.$store.state.baseUrl + 'file/img', formdata1, config).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.imgArr.push(res.data.data)
that.showImgArr.push(rst.base64)
console.log(that.showImgArr)
}
})
return rst;
}).always(function (e) {

e.target.value = null;
})
} else {
AlertModule.show({
title: "抱歉，最多只能选择9张图片"
})
}
},

toPostCircle() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
content: that.content,
uid: that.uid,
imgurls: that.imgArr.join(',')
};
console.log(JSON.stringify(that.imgArr.join(',')))
that
.$http("post", baseUrl + "api/Circle", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "发表成功！",
onHide: function () {
that.$router.push({
name: 'circle'
})
}
})

}
});
}
}
};



