
import Vue from 'vue'
import YDUI from 'vue-ydui' /* 相当于import YDUI from 'vue-ydui/ydui.rem.js' */
import 'vue-ydui/dist/ydui.rem.css'
import 'vue-ydui/dist/ydui.base.css'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'

import {
mapState
} from 'vuex'
Vue.use(YDUI)
export default {
name: 'bindTel',
data() {
return {
telNum: '',
msg: '',
count: '获取',
btnDisable: false,
timer: null

}
},
computed: {
...mapState(['baseUrl'])
},
mounted: function () {


},
components: {
AlertModule,
Alert
},
methods: {

getCode() {
var that = this;
if (that.regTel) {

that.$http('get', this.baseUrl + 'api/WebUser/Sms', {
mobile: that.telNum,
type: 'login'
}).then(function (res) {
if (res.data.code == '00') {
console.log('获取验证码成功了');
const TIME_COUNT = 60;
if (!that.timer) {
that.count = TIME_COUNT;
that.show = false;
that.timer = setInterval(() => {
if (that.count > 1 && that.count <= TIME_COUNT) {
that.count--;
that.btnDisable = true;
} else {
that.show = true;
clearInterval(that.timer);
that.timer = null;
that.count = '重新获取';
that.btnDisable = false;
}
}, 1000)
}
} else {
AlertModule.show({
title: res.data.msg
})
}
})
}

},


regTel() {
console.log('点击了')
var that = this;

var flag = this.checkTel(this.telNum);
var flag2 = false;
switch (flag) {
case 1:
AlertModule.show({
title: '请输入电话号'
})
break;
case 2:
AlertModule.show({
title: '您输入的电话号格式不正确'
})
break;
case 3:

that.getCode()
flag2 = true;
break;
}

return flag2;

},

gotoBind() {

var that = this;
var openid=localStorage.getItem('openid');
var imgurl=localStorage.getItem('imgurl')

var data = {
openid: openid,
mobile: that.telNum,
smsCode: that.msg,
imgurl: imgurl,
};

that.$http('post', this.baseUrl + 'api/WebUser', data).then(function (res) {
if (res.data.code == '00') {
that.$store.commit('setuid', res.data.data.id)
localStorage.setItem('uid',res.data.data.id)
console.log(that.$store.state.uid)

that.$router.push({
path: '/choose_identity'
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})

},

GetQueryString(name) {
var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
var r = window.location.search.substr(1).match(reg);
if (r != null) return unescape(r[2]);
return null;
}
}
}



