export default {
  data() {
    return {
      active: false,
    }
  },
  props:['title','arr','nickName'],
  mounted() {
       console.log('执行')
       this.getData();
  },
  methods: {
    getData() {
      for (var i in this.arr) {
        this.arr[i].active = false;
        this.arr[i].name = this.arr[i][this.nickName]
        console.log(this.arr[i].name)
      }
    },
    // 每个item的点击书简
    addActive(index) {
      this.arr[index].active = !this.arr[index].active;
    },
    // 提交点击得到的结果
    getResult(){
      var arr=[];
      for(let i in this.arr){
        if(this.arr[i].active==true){
            arr.push(this.arr[i])
        }
      }
      var multiResult={
        title:this.title,
        arr:arr,
        nickName:this.nickName
      }
      sessionStorage.setItem('multiResult',JSON.stringify(multiResult))

      this.$router.go(-1)
    },
    beforeRouteLeave(to, from, next) {
      if (to.path == "/myMainPage") {
        to.meta.keepAlive = true;
      } else {
        to.meta.keepAlive = false;
      }
      next();
    }


  }
}



