
import sliderPopupPicker from '@/components/sliderPopupPicker'
import uploader from '@/components/uploader'
import axios from 'axios'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
uploader,
sliderPopupPicker
},
data(){
return {
degree:'',
leftText1:'学历',
rightText:'不限',
eduList:[['小学','初中','高中','中专','专科','本科','研究生']],
isios: false,
frontUrl: '',
backUrl: ''
}
},
mounted() {

var ua = navigator.userAgent.toLowerCase();

var isIos = ua.indexOf("iphone") != -1 || ua.indexOf("ipad") != -1;
if (isIos) {
this.isios = true;
} else {
this.isios = false;
}
},
methods:{
frontImg () {
var file = document.getElementById("upload_file1");
file.click()
},
backImg() {
var file1 = document.getElementById("upload_file2");
file1.click()
},
changeResultEdu(val){
this.degree=val.value
this.rightText=val.value
},

chooseImageFront() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file1").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
};

axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var showUrl = 'http://nian.im/storage/' + res.data.data
that.frontUrl = showUrl

}
})
},


chooseImageBack() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file2").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头

axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var showUrl = 'http://nian.im/storage/' + res.data.data
that.backUrl = showUrl

}
})
},

submitMsg() {
var that = this;
var postData = {
degree:that.degree,
certificate:that.frontUrl,
}
var flag=true;
for(var i in postData){
if(!Boolean(postData[i])){
AlertModule.show({
title:"请填写全部信息"
})
flag=false
break;
}
continue;
}
if(flag){
postData.uid=that.$store.state.uid;
postData.othersCertificate=that.backUrl
var baseUrl = this.$store.state.baseUrl;
that.$http("post", baseUrl + "api/WebUser/Certificate/Edu",postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "提交成功！",
onHide(){
that.$router.go(-1)
}

})
}

})
}

}

}

}



