
import AlertModule from 'vux/src/plugins/alert/module'

import wx from 'weixin-js-sdk';
export default {
data() {
return {
moneyList: [{
id: 0,
value: 1,
isChoose: true
}, {
id: 1,
value: 5,
isChoose: false
}, {
id: 2,
value: 10,
isChoose: false
}, {
id: 3,
value: 20,
isChoose: false
}, {
id: 4,
value: 50,
isChoose: false
}, {
id: 5,
value: 99,
isChoose: false
}],
uid:this.$route.query.uid,
amount:1
}
},
components:{
AlertModule
},
mounted(){
this.wxConfig()
},
methods: {
chooseItem(index) {
for (var i in this.moneyList) {
this.moneyList[i].isChoose = false;
}
this.moneyList[index].isChoose = true;
this.amount=this.moneyList[index].value;
},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // 开启调试模式,
appId: result.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
timestamp: result.timestamp, // 必填，生成签名的时间戳
nonceStr: result.nonceStr, // 必填，生成签名的随机串
jsApiList: ["chooseWXPay"],
signature: result.signature, // 必填，签名，见附录1
jsToken: result.jsToken 
});
}
})
},
giveMoney(){
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/Task/Red',
{
uid:that.uid,
amount:this.amount,
taskId:this.$route.query.taskId
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var  result=JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
package: result.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: result.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: result.paySign, // 支付签名
success: function (res) {

that.$router.push({
path:'/mineIndex'
})
}
});
})
}

})
}
}
}



