
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {

uid: this.$store.state.uid,
mobile: ''

}
},
components: {
AlertModule
},
methods: {
addMediator() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/Mediator', {
uid: that.uid,
mobile: that.mobile
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "提交成功",
onHide() {
this.$router.push({
path: '/myNetwork'
})
}
})
}

})
},
goBack() {
this.$router.go(-1)
}
}
}



