import common from "@/assets/js/common";
export default {
  data() {
    return {
      isSaw: false,
      uid: this.$store.state.uid,
      msgList: [],
      mailId:''
    };
  },
  mounted() {
    this.getMsgList();
  },
  methods: {
    ...common,
    // 获取私信列表/api/UserMsg/List
    getMsgList() {
      var that = this;
      var baseUrl = this.$store.state.baseUrl;
      // alert(that.uid)
      that
        .$http("get", baseUrl + "api/UserMsg/List", {
          uid:that.uid
        })
        .then(function(res) {
          console.log(res.data.data);
          var result = res.data.data;
          that.mailId=result.id;
           that.msgList = result;
        });
    },
    toShowMailDetail(id) {
      this.$router.push({
        path:"/mailDetail",
        query:{
           mailId:id
        }
      })
    }
  }
};



