
import common from '@/assets/js/common'
import sliderPopupPicker from '@/components/sliderPopupPicker'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
name: "fill_in_information",
components: {
PopupPicker,
Group,
sliderPopupPicker
},
data() {
return {
title1: '年级',
title2: "科目",

sliderSubjectArr: [],
sliderClassArr: [],
leftText1: '年级',
leftText2: '科目',
rightText1: '不限',
uid: this.$store.state.uid,
value1: [],
value2: [],
tempVla: '',
tempvalue: [],
role: '',
data: {},
imgurl: localStorage.getItem('imgurl')
}
},
mounted() {
this.getCategory()
this.getGrade()
this.role = this.$route.query.role;
},
computed: {

},
methods: {
...common,
changeGrade(val) {
this.tempVla = val[val.length - 1];
console.log(val)
},
submitMsg() {
var that = this;
if (!Boolean(that.data.classNo)) {
AlertModule.show({
title: '请填写信息'
})
} else {
that.$http('post', that.$store.state.baseUrl + 'api/WebUser/Author', {
uid: that.uid,
classNo: that.data.classNo,
subject: that.data.category == undefined ? 0 : that.data.category
}).then(function (res) {
if (res.data.code == '00') {
that.$http('get', that.$store.state.baseUrl + 'api/WebUser/' + that.$store.state.uid).then(function (res) {
if (res.data.code == '00') {
console.log(res.data.data)
var result = res.data.data;
var type = result.type == "author" ? '作者' : '书商';
var clas = result.authorInfo.classParent == '' ? '' : result.authorInfo.classParent.substr(0, 1);
var category = result.authorInfo.subject == '' ? '' : result.authorInfo.subject.substr(0, 1);;
var nickname = clas + category + type;
console.log(nickname);
var postData = {
uid: result.id,
nickname: nickname
}

var url = that.$store.state.baseUrl + '/api/WebUser/' + result.id;

that.$http('put', url, postData).then(function (res) {
if (res.data.code == '00') {
that.$router.push({
name: 'index',
path: 'index',
})
}
})

}
})
}
})
}

},
changeResultGrade(val) {
console.log(val)

this.data.classNo = val.value;
},
changeResultKM(val) {
console.log(val)

this.data.category = val.value;
},
hide: function () {
this.tempvalue = this.tempvalue.concat(this.tempVla);

this.value2 = this.tempvalue;
},
format1: function (value, name) {
return `${value[0]}/${value[1]}/${value[2]}`
}
}
}



