
import wx from 'weixin-js-sdk';
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
name: "privateMsg",
components: {
CheckIcon,
AlertModule
},
data() {
return {
yuan1: true,
toAuthod: false,
hongBaoList: [{
yuan: 1,
isChecked: true
},
{
yuan: 5,
isChecked: false
},
{
yuan: 10,
isChecked: false
},
{
yuan: 20,
isChecked: false
},
{
yuan: 50,
isChecked: false
},
{
yuan: 99,
isChecked: false
}
],
content: "",

fromUid: this.$store.state.uid,

uid: this.$route.query.writerId
};
},
mounted() {
this.wxConfig();
this.getUser(this.uid)
},
methods: {
chooseItem(index) {
for (var i in this.hongBaoList) {
this.hongBaoList[i].isChecked = false;
}
this.hongBaoList[index].isChecked = true;
},
getUser(uid) {
var that = this;
var baseUrl = that.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
var type = res.data.data.type;

if (type == 'business') {
that.toAuthod = false
} else {
that.toAuthod = true;

}
})

},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // 开启调试模式,
appId: result.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
timestamp: result.timestamp, // 必填，生成签名的时间戳
nonceStr: result.nonceStr, // 必填，生成签名的随机串
jsApiList: ["chooseWXPay"],
signature: result.signature, // 必填，签名，见附录1
jsToken: result.jsToken 
});
}
})
},

sendPrivateMsg() {
var that = this;
var baseUrl = this.$store.state.baseUrl;

var postData = {
content: that.content,
uid: that.fromUid,
toUid: that.uid,
amount: 0
};
that.$http("post", baseUrl + "api/PrivateMsg", postData).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "发送成功",
onHide() {
that.$router.push({
path: '/writerDetail',
query: {
writerId: that.uid
},
meta: {
keepAlive: true
}
})
}
});

}
});

},

sendMsgAndMoney() {
var that = this;

var yuan;
for (var i in this.hongBaoList) {
if (this.hongBaoList[i].isChecked) {
yuan = this.hongBaoList[i].yuan;
}
}
var baseUrl = this.$store.state.baseUrl;

var postData = {
content: that.content,
uid: that.fromUid,
toUid: that.uid,
amount: yuan
};
that.$http("post", baseUrl + "api/PrivateMsg", postData).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
package: result.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: result.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: result.paySign, // 支付签名
success: function (res) {

that.$router.push({
path: '/writerDetail',
query: {
writerId: that.uid
}
})
}
});
})

}
});
}
}
};



