
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'


export default {
props: ['leftTitle', 'num', 'icon','noBorder','link','writerId'],
components: {
Cell,
Badge
},
methods:{
gotoSeePage(){
this.$router.push({
path:this.link,
query:{
'writerId':this.writerId
},
meta:{
keepActive:false
}
})
}
}
}




