
import common from "@/assets/js/common";
import axios from "axios";
import qs from "qs";
import datetimePicker from "@/components/datetimePicker";
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
XButton,
Group,
PopupPicker,
Datetime,
datetimePicker,
AlertModule
},
data() {
return {
finalPrice: 0,
beginDate: "",
endDate: "",
leftText2: "每次时长",
time: [],
timeArr: [],
times: [],
timesArr: [],
leftText1: "阶段次数",

chooseWriterId: this.$route.query.chooseWriterId,


canTalk: false,

taskId: this.$route.query.taskId,
taskObj: {},
stage: 0,
sendSuccess: this.$route.query.sendSuccess,
gapDay: 1,
eachPrice: 0,
timesList: [],
serviceRate: 0,
adjustmentRate: 0,
postUser: {},
confirmList: [],
nicknameArr: [],
talkContent:'',
talkList:[],
infoObj:{}
};
},

mounted() {
var arr = [];
for (var i = 1; i <= 12; i++) {
arr.push(i);
}
this.timesArr.push(arr);
var arr2 = [];
for (var i = 1; i <= 24; i++) {
arr2.push(i);
}
this.timeArr.push(arr);

this.getMissiondetail(this.taskId);

this.getBaseRate();

this.getConfirmWriter(this.taskId);
console.log("当前任务对象");

this.getUser("postUser", this.$store.state.uid);
this.getTalkList();
this.getStageMsg(this.taskId)
},
methods: {
...common,

goToTalk(){
this.canTalk=true;
},

sendTalkMsg(){
var that = this;
var baseUrl = that.$store.state.baseUrl;
that
.$http("post", baseUrl + "api/TaskTalk", {
uid:that.$store.state.uid,
taskId: that.taskId,
content:that.talkContent
})
.then(function(res) {
that.getTalkList()
that.canTalk=false;

});

},

getTalkList() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/TaskTalk/List", {
taskId: that.taskId
})
.then(function(res) {
that.talkList=res.data.data;
console.log("留言列表");
console.log(that.talkList)
});
},


getUser(who, uid) {
var that = this;
var baseUrl = that.$store.state.baseUrl;
var user;
that.$http("get", baseUrl + "api/WebUser/" + uid).then(function(res) {
if (who == "postUser") {
that.postUser = res.data.data;
}
console.log("获取的用户对象");
console.log(that.postUser);
});

},

getConfirmWriter(id) {
var that = this;
var postData = {
taskId: id,
status: "2"
};
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
postData
)
.then(function(res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
var nicknameArr = [];
for (var i in result) {
nicknameArr.push(result[i].user.nickname);
}
that.confirmList = result;
that.nicknameArr = nicknameArr;
console.log("已确认列表");
console.log(nicknameArr);
}
});
},





getMissiondetail(id) {
var that = this;
that
.$http(
"get",
that.$store.state.baseUrl +
"api/Task/" +
id



)
.then(function(res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.taskObj = res.data.data;

}
});
},

getStageMsg(id){
var that = this;
that
.$http(
"get",
that.$store.state.baseUrl +
"api/Task/Stage/" +
id



)
.then(function(res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var  result=res.data.data
result.beginDate=result.beginDate.substr(0,10);
result.endDate=result.endDate.substr(0,10);
that.infoObj = result;



}
});
},

sendRefuse(){
var that = this;
var postData = {
taskId: that.taskId,
status: "4"
};
that
.$http(
"post",
that.$store.state.baseUrl +
"api/Task/Stage/Confirm" ,
postData
).then(function(res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "拒绝成功"
});
}
});

},

sendConfirm() {
var that = this;
var postData = {
taskId: that.taskId,
status: "2"
};
that
.$http(
"post",
that.$store.state.baseUrl +
"api/Task/Stage/Confirm" ,
postData
).then(function(res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "确认成功",
onHide(){
that.$router.push({
name:'myEnrollMission'
})
}

});

}
});


},

timesChange(val) {

this.stage = val.join("");
},

timeChange(val) {},

beginDateChange(val) {
this.beginDate = val;
},

endDateChange(val) {
this.endDate = val;
}
}
};



