
import common from "@/assets/js/common";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
components: {
Flexbox,
FlexboxItem,
AlertModule,
Previewer,
TransferDom
},
data() {
return {
circleId: '',
circleDetail: {},
content: "",
isFocus: false,
isCommentOther: false,
commentUid: "",
uid: this.$store.state.uid,
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {
x: rect.left,
y: rect.top + pageYScroll,
w: rect.width
}


}
}
};
},
mounted() {
this.circleId = this.$route.query.circleId;


this.getCircleDetail();

},
methods: {
...common,

getCircleDetail() {
var that = this;
var post={
loginUid:that.uid
}
that
.$http("get", that.$store.state.baseUrl + "api/Circle/" + that.circleId,post)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
if (result.imgurls != '') {

if (result.imgurls.indexOf(',') == -1) {

var Arr = []
var obj = {
src: that.$store.state.imgUrl+result.imgurls
}
Arr.push(obj)
result.imgurls = Arr;
} else {
result.imgurls = result.imgurls.split(',')
var arr2 = []
for (var k in result.imgurls) {
var obj = {};
obj.src = that.$store.state.imgUrl+result.imgurls[k];
arr2.push(obj)
}
result.imgurls = arr2;
}
}
that.circleDetail = result;
console.log("圈子详情");
console.log(that.circleDetail);
}
});
},
show(index) {
console.log(index)
console.log(this.$refs.previewer)
this.$refs.previewer.show(index)
},

sendMsg(event) {
console.log(this.isCommentOther);
if (this.isCommentOther) {



event.preventDefault(); 
var that = this;
var postData = {
uid: this.uid,
circleId: that.circleDetail.id,
content: that.content,
commentId: that.commentUid
};
that
.$http(
"post",
that.$store.state.baseUrl + "api/Circle/comment",
postData
)
.then(function (res) {
if (res.data.code != "00") {
that.isCommentOther = false;
AlertModule.show({
title: res.data.msg
});
} else {
that.getCircleDetail();
that.content = "";
}
});

} else {


event.preventDefault(); 
var that = this;
var postData = {
uid: this.uid,
circleId: that.circleDetail.id,
content: that.content
};
that
.$http(
"post",
that.$store.state.baseUrl + "api/Circle/comment",
postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.getCircleDetail();
that.content = "";
}
});

}
},

toComment(uid) {
this.$refs["sendMsgInput"].focus();
},

toCollection(id) {
var that = this;
var postData = {
uid: this.uid,
circleId: id
};
that
.$http(
"post",
that.$store.state.baseUrl + "api/Circle/Collection",
postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.getCircleDetail();
}
});
},

toLike(id) {
var that = this;
var postData = {
uid: this.uid,
circleId: id
};
that
.$http("post", that.$store.state.baseUrl + "api/Circle/Like", postData)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.getCircleDetail();
}
});
},

toFellow(i) {
var that = this;
var postData = {
uid: that.uid,
followingUid: that.circleDetail.uid
};
that
.$http(
"post",
that.$store.state.baseUrl + "api/UserFollowing",
postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.getCircleDetail();
}
});
this.circleList[i].follow = !this.circleList[i].follow;
},

toReply(uid) {
this.commentUid = uid;
this.$refs["sendMsgInput"].focus();
this.isCommentOther = true;
}
}
};



