export default {
    data() {
        return {
            //任务列表
            postList: [],
            postData: {},
            tabList: [{
                    name: "全部",
                    active: true,
                    id: ''
                },
                {
                    name: "待确认",
                    active: false,
                    id: 7
                },
                {
                    name: "执行中",
                    active: false,
                    id: 3
                },
                {
                    name: "已结束",
                    active: false,
                    id: 4
                }
            ],
            uid: this.$store.state.uid,
            userData: {}
        };
    },
    mounted() {

        this.getMIssionlist();
        this.getUser(this.$store.state.uid)
    },
    methods: {
        getUser(uid) {
            var that = this;
            var baseUrl = that.$store.state.baseUrl;
            that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
                that.userData = res.data.data;
            })

        },
        // 查看任务详情
        toSeemyPostMissionDetail(i) {
            var id = this.postList[i].id;
            var taskStatus = this.postList[i].status;
            var type = this.userData.type;
            // 我发布的任务，所以我是书商
            if (taskStatus == '3') {
                this.$router.push({
                    path: "/missionInExecutionBuss",
                    query: {
                        id: id,
                        isMyTask: true
                    }
                });
            } else {
                this.$router.push({
                    path: "/missionDetail",
                    query: {
                        id: id,
                        isMyTask: true
                    }
                });
            }

        },
        // 点击tab
        chooseTab(index) {
            for (let i in this.tabList) {
                this.tabList[i].active = false;
            }
            this.tabList[index].active = true;
            var status = this.tabList[index].id;
            this.getMIssionlist(status);
        },
        // 获取任务列表
        getMIssionlist(status) {
            var that = this;
            that.postData.uid = that.uid;
            // if(that.status){
            that.postData.status = status;
            // }
            console.log(that.postData);
            that
                .$http(
                    "get",
                    that.$store.state.baseUrl + "api/Task/List",
                    that.postData
                )
                .then(function (res) {
                    that.postList = res.data.data;
                    if (res.data.code == "00") {
                        var result = res.data.data;
                        // console.log(result.webUser.type)
                        for (var i in result) {
                            var arr = result[i].priceRange.split("-");
                            result[i].priceMin = arr[0];
                            result[i].priceMax = arr[1];

                            if (result[i].taskType == 1) {
                                result[i].borderColor = "zz";
                            } else if (result[i].taskType == 2) {
                                result[i].borderColor = "ss";
                            } else {
                                result[i].borderColor = "sz";
                            }
                            // =====================
                            switch (result[i].status) {
                                case 1:
                                    result[i].status = "发布中";
                                    break;
                                case 2:
                                    result[i].status = "已选定服务人";
                                    break;
                                case 3:
                                    result[i].status = "执行中";
                                    break;
                                case 4:
                                    result[i].status = "已结束";
                                    break;
                                case 6:
                                    result[i].status = "已取消";
                                    break;
                                case 7:
                                    result[i].status = '已有人报名';
                                    break;
                                case 99:
                                    result[i].status = "已失败";
                                    break;
                            }
                        }

                        that.postList = res.data.data;
                    } else {
                        // AlertModule.show({
                        //   title: res.data.msg
                        // })
                    }
                });
        }
    }
};



