
import axios from 'axios'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import radioPicker from '@/components/radioPicker'
export default {
data() {
return {
missionId: this.$route.query.missionId,
stageDetail: JSON.parse(this.$route.query.stageDetail),
title: '是否公开？',
radiosArr: [{
value: 1,
text: '是',
selected: true
},
{
value: 2,
text: '否',
selected: false

}
],
mainData: []

}
},
mounted() {
var that = this;

for (var i in that.stageDetail.infos) {
var obj = {};
obj.content = that.stageDetail.infos[i].content;
if(that.stageDetail.infos[i].content.trim()!=''){
obj.disabled=false;
}else{
obj.disabled=true;
}
obj.date = that.stageDetail.infos[i].date;
obj.publ = that.stageDetail.publ;
obj.id = that.stageDetail.infos[i].id
that.mainData.push(obj)
}
},
components: {
Swiper,
GroupTitle,
Group,
SwiperItem,
XButton,
Divider,
Checker,
CheckerItem,
radioPicker,
AlertModule
},
methods: {

getRadioValue(val) {
if (val == 1) {
this.stageDetail.publ = true
} else {
this.stageDetail.publ = false
}
},

savePlan() {
var that = this;
var flag = false;
for (var i in that.mainData) {
delete that.mainData[i].date;
delete that.mainData[i].disabled;
that.mainData[i].publ = that.stageDetail.publ;

that.$http('put', that.$store.state.baseUrl + 'api/Task/Content/' + that.mainData[i].id, that.mainData[i]).then(function (res) {
if (res.data.code != '00') {
flag = false;

} else {
flag = true;

}

})
}
that.$router.push({
path: '/missionInExecutionAuth',
query: {
id: that.missionId
}
})

}
}
}



