
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name:'slidePicker',
data(){
return {
pickerIsShow:false,
chooseResult:[],
rightText:'不限'
}
},
props:['gradesArr','leftText','id'],
components:{
Picker,
Group,
PopupPicker
},
mounted(){
console.log('传入的数组')
console.log(this.gradesArr)
},
methods:{

changeValue(value) {
this.pickerIsShow=!this.pickerIsShow;
},
change(value) {
console.log('value'+value)
var  name=this.$refs.picker3.getNameValues(value);
var  value=value[0];

this.rightText=name;
console.log('右边的文字name'+this.rightText)

console.log('value'+value)
this.$emit('changeResult',{
name:name,
value:value
})
}


}
}



