
import common from "@/assets/js/common"
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
userData: {},
tags: [],
uid: this.$store.state.uid,
userTags: []
}
},
components: {
AlertModule
},
mounted() {

this.getWebUser(this.uid);

this.getTasgs()



},
methods: {
...common,

getWebUser(uid) {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/WebUser/' + uid, {
loginUid: that.$store.state.uid
}).then(function (res) {
that.userTags = res.data.data.authorInfo.userTags;

for (var i in that.tags) {
for (var j in that.userTags) {
if (that.tags[i].tagName == that.userTags[j].tag) {
console.log(that.tags[i].tagName+"============" + that.userTags[j].tag)
that.tags[i].active = true;
}
}
}

console.log("我的标签列表")
console.log(that.userTags);
console.log(that.tags);
})
},












chooseTags(i) {
this.tags[i].active = !this.tags[i].active;
},

saveMyTags() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var arr = []
for (var i in this.tags) {
if (this.tags[i].active == true) {
arr.push(this.tags[i].tagName)
}
}
if (arr.length == 0) {
AlertModule.show({
title: '请选择标签'
})
} else {
var postData = {
uid: this.$store.state.uid,
tags: arr.join(',')
}
that.$http('post', baseUrl + 'api/UserTag/User', postData).then(function (res) {

console.log("提交标签")
if (res.data.code == '00') {
AlertModule.show({
title: '提交成功',
onHide() {
that.$router.push({
path: '/mineIndex'
})
}
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
}

}
},
components: {

}
}



