
import uploader from "@/components/uploader";
import axios from 'axios'
import AlertModule from 'vux/src/plugins/alert/module'

import common from "@/assets/js/common";
export default {

components: {

uploader

},

data() {
return {
firstName: "",
secondName: "",
idCard: "",
isios: false,
frontUrl: '',
backUrl: '',
shortName: ''
};

},
components: {
AlertModule
},

mounted() {
var that = this;
that.getNickname()

var ua = navigator.userAgent.toLowerCase();

var isIos = ua.indexOf("iphone") != -1 || ua.indexOf("ipad") != -1;
if (isIos) {
that.isios = true;
} else {
that.isios = false;
}

},

methods: {
...common,

getNickname() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var uid = that.$store.state.uid;
that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
that.shortName = res.data.data.nickname;




})
},
frontImg() {
var file = document.getElementById("upload_file1");
file.click()
},
backImg() {
var file1 = document.getElementById("upload_file2");
file1.click()
},

chooseImageFront() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file1").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头
axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var showUrl = 'http://nian.im/storage/' + res.data.data
that.frontUrl = showUrl
}

})

},


chooseImageBack() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file2").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头
axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var showUrl = 'http://nian.im/storage/' + res.data.data
that.backUrl = showUrl


}

})

},



submitMsg() {
var that = this;
var postData = {
firstName: that.firstName,
secondName: that.secondName,
idCard: that.idCard,
cardFront: that.frontUrl,
cardReverse: that.backUrl
}
var flag = true;

for (var i in postData) {
if (!Boolean(postData[i])) {
AlertModule.show({
title: "请填写全部信息"
})
flag = false
break;
}
continue;
}
if (flag) {
postData.uid = that.$store.state.uid;
var baseUrl = this.$store.state.baseUrl;
that.$http("post", baseUrl + "api/WebUser/Certificate/RealName", postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var str1 = that.shortName.slice(0, -2);
var str2 = that.shortName.slice(-2)
that.shortName = str1 + that.firstName + str2;
var url = that.$store.state.baseUrl + '/api/WebUser/' + that.$store.state.uid;
that.$http('put', url, {
nickname: that.shortName
}).then(function (res) {
if (res.data.code == "00") {
AlertModule.show({
title: "保存成功！",
onHide() {
that.$router.push({
path: '/mineIndex'
})
}
});
that.getWebUser()
} else {
AlertModule.show({
title: res.data.msg
});
}
});

}

})
}

}

}

};



