
import slidePicker from '@/components/slidePicker'
import sliderPopupPicker from '@/components/sliderPopupPicker'
import radioPicker from '@/components/radioPicker'
import datetimePicker from '@/components/datetimePicker'
import common from '@/assets/js/common'
import Range from 'vux/src/components/range/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
data() {
return {
bookseller: [],



classNo: '',
category: '',
taskType: '', //出版方式
priceType: '',
area: '',
introduction: '',
content: '',
priceMin: '',
priceMax: '',

myarea: [],
addressData: ChinaAddressV4Data,
date: '',
sliderSubjectArr: [],
sliderClassArr: [],



















gradesArr: [],
leftText1: '年级',
rightText1: '不限',
subjectArr: [],
priceArr: [],
slidePriceArr: [],
coordination: [
['不限', '作者拜访', '书商拜访', '远程协作']
],

leftText2: '科目',
leftText3: '价格区间',
leftText4: '协作方式',
leftText5: '协作区域',
leftText6: '招标书商人数',

booksellerArr: [

],


radioTitle: '选择性别',
publish_title: '出版方式',
radiosArr: [{
value: 1,
text: '独资',
selected: true
},
{
value: 2,
text: '合资',
selected: false
},
],
postData: {
priceType: '',
classNo: '',
category: '',
priceType: '',
priceRange: '',
busniessCount: '',
deadline: '',
coordination: '',
area: '',
introduction: ''
},
userData: {},
writerId: this.$route.query.writerId
}
},
components: {
datetimePicker,
XTextarea,
Range,
Popup,
XSwitch,
Cell,
XButton,
Group,
Picker,
sliderPopupPicker,
radioPicker,
ChinaAddressV4Data,
XAddress,
Alert,
slidePicker,
PopupPicker,
AlertModule
},
mounted() {
this.getCategory();
this.getGrade();
this.getPriceArr()

this.getUser(this.$route.query.writerId);


var arr = [];
for (var i = 1; i <= 1000; i++) {
arr.push(i)
};
this.booksellerArr.push(arr)
},
methods: {

...common,

onAddressChange(val) {
this.postData.area = value2name(this.myarea, ChinaAddressV4Data).split(' ').join(",");
},

booksellerGrade(val) {

if (this.radiosArr[0].selected == true && val[0] != 1) {
AlertModule.show({
title: "抱歉，独资方式只能选择一名书商"
})
} else {
this.postData.busniessCount = val.join('')
}

},
getUser(uid) {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
that.userData = res.data.data;
if (that.userData.type == 'author') {

that.postData.taskType = '1'
} else {

that.postData.taskType = '2'
}
})
},
















changeResultWay(val) {
console.log(val)
this.postData.coordination = val[0];
},


changeResultGrade(value) {
var that = this;
that.postData.classNo = value.name;

},

changeResultKM(value) {
this.postData.category = value.name;
console.log(this.postData.category)
},


taskOrientation() {
var that = this;

that.postData.priceType = 1;

that.postData.busniessCount = 1;
if (!that.postData.classNo) {
that.postData.classNo = '';
}
if (!that.postData.category) {
that.postData.category = '';
}
if (!that.postData.coordination) {
that.postData.coordination = '';
}
if (!that.postData.area) {
that.postData.area = '';
}
if (!that.postData.introduction) {
that.postData.introduction = '';
}
that.postData.introduction = that.introduction;
that.postData.content = that.content;
that.postData.uid = this.$store.state.uid;


that.postData.deadline = '2199-01-01';

for (var i in that.postData) {
that.postData[i] = that.postData[i].toString()
}
console.log(that.postData)

var dataArr = [that.postData.classNo, that.postData.category, that.postData.taskType, that.postData.priceType, that.postData.coordination, that.postData.area, that.postData.introduction];
var nameArr = ['年级', "科目", '出版方式', '出资方式', '协作方式', '写作区域', '简述']

if (that.isEmpty(dataArr, nameArr).length != 0) {
AlertModule.show({
title: "请填写：" + that.isEmpty(dataArr, nameArr).join(',')
})
}else{

that.$http('post', that.$store.state.baseUrl + 'api/Task', that.postData).then(function (res) {
console.log(res.data)
var taskId = res.data.data.id
if (res.data.code == '00') {

var postData = {
taskId:taskId,
uid: that.writerId
}
that.$http('post', that.$store.state.baseUrl + 'api/Task/Apply', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg,
})
} else {

var postData = {
id: res.data.data.id,
status: "2"
};
that.$http("post",
that.$store.state.baseUrl + "api/Task/Apply/Select",
postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
})
} else {
var arr = []
console.log(that.writerId)

that.$router.push({
path: "/replyDetails",
query: {
isNew: true,
taskId: taskId,
chooseWriterId: arr.push(that.writerId),
sendSuccess: false
}
})
}
});
}

})
}
})

}

}
}}



