export default {
    data(){
        return {
            msgList:[],
            postData:{}
        }
    },
    mounted(){
        this.getActiveMsg()
    },
    methods:{
        getActiveMsg(){
             var that = this;
            that.postData.uid = that.$store.state.uid;
           
            that.$http('get', that.$store.state.baseUrl + 'api/Circle/Dynamic', that.postData).then(function (res) {
                if (res.data.code == '00') {}
                    that.msgList=res.data.data;
                })
        }
    }
};



