
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
missionId: this.$route.query.missionId,
stageDetail: JSON.parse(this.$route.query.stageDetail),
times: 0,
date: '',
id:'',
content:''
}
},
mounted() {
var that = this;
console.log(that.stageDetail)
for (var i in that.stageDetail.infos) {
if (!that.stageDetail.infos[i].success) {
that.times = i;
that.date = that.stageDetail.infos[i].date;
that.id=that.stageDetail.infos[i].id
break;
} else {
continue;
}
}
},
methods: {

submitRefister() {
var that=this;
that.$http('put', that.$store.state.baseUrl + 'api/Task/RegisterContent/' + that.id, {
registerContent:that.content
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg,
})

} else {
AlertModule.show({
title: '提交成功',
onHide() {
that.$router.push({
path: '/missionInExecutionAuth',
query: {
id: that.missionId
}
})
}
})
}

})
}
}
}



