export default {
    data() {
        return {
            problemList: []
        }
    },
    mounted() {
        this.getProblem()
    },
    methods: {
        getProblem() {
            var that = this;
            var baseUrl = this.$store.state.baseUrl;
            that.$http('get', baseUrl + 'api/CommonProblem/List', {}).then(function (res) {
                console.log("系统消息")
                that.problemList = res.data.data;
            })
        }
    }
}



