
import writers from '@/components/writer/writer'
import common from '@/assets/js/common'

import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
hang2IsShow: true,
authodShortName: '',
workAge: '',
networkCount: '',

userData: {},
classNo: '',
subject: '',
selfCon: '',
coordination: '',
jointCost: '',
area: '',
classNo: '',
writerId: this.$route.query.writerId,
userType: '',
uid: this.$store.state.uid,
taskObj: {},
taskList: [],
circleObj: {},
isFellow: false,
soleCost: '',
experience: '',
achievement: "",
books: '',
isLocalUser: false,
userTags: [],
selfTag: [],
otherTags: [],
circleList: [],

}
},
components: {
writers,
XButton,
AlertModule
},
mounted() {

this.getWebUser(this.writerId);

this.getMIssionlist()

this.getCircleLoist();

if (this.writerId == this.$store.state.uid) {
this.isLocalUser = true
}

this.getTasgs(this.uid)

},
methods: {
...common,

gotoSendOrientationTask() {
var that = this;
that.$router.push({
path: "/taskOrientation",
query: {
writerId: that.writerId
}
})
},

fellowThisWriter() {
var that = this;
var postData = {
uid: that.uid, //当前用户id
followingUid: that.writerId 
}
that.$http('post', that.$store.state.baseUrl + 'api/UserFollowing', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "操作成功！",
onHide() {
that.getWebUser(that.writerId);
}
})
that.isFellow = true
}
})
},

gotoSendMsg() {
var that = this;

that.$router.push({
path: '/privateMsg',
query: {
writerId: that.writerId
}
})
},

getCircleLoist() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/Circle/List', {
loginUid: that.uid,
uid: that.writerId
}).then(function (res) {
var result = res.data.data;
that.circleList = res.data.data;
if (result.length != 0) {
for (var i in result) {
if (result[i].imgurls != '') {
if (result[i].imgurls.indexOf(',') == -1) {

var Arr = []
var obj = {
src: that.$store.state.imgUrl + result[i].imgurls
}
Arr.push(obj)
result[i].imgurls = Arr;
} else {
result[i].imgurls = result[i].imgurls.split(',')
var arr2 = []
for (var k in result[i].imgurls) {
var obj = {};
obj.src = that.$store.state.imgUrl + result[i].imgurls[k];
arr2.push(obj)
}
result[i].imgurls = arr2;
}
}
}
that.circleObj = result[0]
}
console.log('圈子')
console.log(result)
























})
},

getMIssionlist(status) {
var that = this;
that.postData = {
uid: that.writerId
}

console.log(that.postData)
that.$http('get', that.$store.state.baseUrl + 'api/Task/List', that.postData).then(function (res) {

if (res.data.code == '00') {
var result = res.data.data;

for (var i in result) {
var arr = result[i].priceRange.split('-')
result[i].priceMin = arr[0];
result[i].priceMax = arr[1];

if (result[i].taskType == 1) {
result[i].borderColor = 'zz'
} else if (result[i].taskType == 2) {
result[i].borderColor = 'ss'
} else {
result[i].borderColor = 'sz'
}
}
console.log('我的任务')
console.log(res.data.data)
that.taskList = res.data.data

that.taskObj = res.data.data[0];
switch (that.taskObj.status) {
case 1:
that.taskObj.status = "发布中";
break;
case 2:
that.taskObj.status = "已选定服务人";
break;
case 3:
that.taskObj.status = "执行中";
break;
case 4:
that.taskObj.status = "已结束";
break;
case 6:
that.taskObj.status = "已取消";
break;
case 7:
that.taskObj.status = '已有人报名';
break;
case 99:
that.taskObj.status = "已失败";
break;
}




















} else {}
})
},
}
}



