import common from "@/assets/js/common"
export default {
    data() {
        return {
              uid: this.$store.state.uid,
           
            myEwm: '',
            userData: {},
            myMediator: [],
            needMediator: []

        }
    },
    mounted() {
        this.getEWM();
        this.getWebUser(this.uid);
        this.getMediator()
    },
    methods: {
        ...common,
        // 获取我的二维码
        getEWM() {
            var that = this;
            var baseUrl = this.$store.state.baseUrl;
            that.$http('get', baseUrl + 'api/WebUser/Qr/' + that.uid).then(function (res) {
                if (res.data.code != '00') {
                    AlertModule.show({
                        title: res.data.msg
                    })
                } else {
                    that.myEwm = res.data.data;
                }

            })
        },
        // 获取我的调解人
        getMediator() {
            var that = this;
            var baseUrl = this.$store.state.baseUrl;
            that.$http('get', baseUrl + 'api/Mediator/List', {
                uid: that.uid
            }).then(function (res) {
                if (res.data.code != '00') {
                    AlertModule.show({
                        title: res.data.msg
                    })
                } else {
                    that.myMediator = res.data.data.myMediator;
                    that.needMediator = res.data.data.needMediator;
                }

            })
        },
        // 添加我的调解人
        addMyMediator() {
            this.$router.push({
                path: '/myMediator'
            })
        }
    }

}



