
import wx from 'weixin-js-sdk';
import common from "@/assets/js/common"
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
money: this.$route.query.amount,
isChoose: false,
uid: this.$store.state.uid,
userData: {},
eachWay: [{
text: '余额支付',
icon: 'icon-qianbao',
payType: 'yue',
isChoose: false
},
{
text: '微信支付',
icon: 'icon-weixin',
payType: 'weixinjs',
isChoose: false
}
]
}
},
components: {
InlineXNumber,
AlertModule,
},
mounted() {
this.getWebUser(this.uid);

this.wxConfig()
},
methods: {
...common,
chooseWay(index) {
var that = this;
for (var i in that.eachWay) {
that.eachWay[i].isChoose = false
}
that.eachWay[index].isChoose = true
},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // 开启调试模式,
appId: result.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
timestamp: result.timestamp, // 必填，生成签名的时间戳
nonceStr: result.nonceStr, // 必填，生成签名的随机串
jsApiList: ["chooseWXPay"],
signature: result.signature, // 必填，签名，见附录1
jsToken: result.jsToken 
});
}
})
},


submit() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
var index;
var payType=''
for (var i in that.eachWay) {
if (that.eachWay[i].isChoose) {
index = i;
payType=that.eachWay[i].payType
}
}
if(!index){
AlertModule.show({
title: "请选择支付方式"
})
}else if (that.money > that.userData.amount && index == 0) {
AlertModule.show({
title: "余额不足，请选择微信支付"
})
} else {
that.$http('post', baseUrl + 'api/Task/Pay/' + that.$route.query.taskId,{
payType:payType
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = JSON.parse(res.data.data)
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
package: result.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: result.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: result.paySign, // 支付签名
success: function (res) {

that.$router.push({
path: '/paySuccess'
})
},
fail: function (res) {

that.$router.push({
path: '/payFail'
})
}
});
})
}

})
}

}
}
}



