
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
data() {
return {
content: "",
email: "",
mobile: "",
nickname: "",
uid: this.$store.state.uid
};
},
components: {
XTextarea,
AlertModule
},
methods: {
submitParent() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
content: that.content,
email: that.email,
mobile: that.mobile,
nickname: that.nickname,
uid: this.$store.state.uid
};
that
.$http("post", baseUrl + "api/Partner", postData)
.then(function(res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
}else{
AlertModule.show({
title: "提交成功",
onHide(){
that.$router.go(-1)
}
})
}
});
}
}
};



