
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
Flexbox,
FlexboxItem,
AlertModule
},
data() {
return {
circleList: [],
uid:this.$route.query.uid,
loginUid: this.$store.state.uid
}
},
mounted() {
this.toSeeMyCollection()
},
methods: {


toCollection(id) {
var that = this;
var postData = {
uid: this.loginUid,
circleId: id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Collection', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.toSeeMyCollection()
}
})
},

toLike(id) {
var that = this;
var postData = {
uid: this.loginUid,
circleId: id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Like', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.toSeeMyCollection()
}
})
},

toComment(id) {
this.$router.push({
path:'/circleDetail',
query:{
circleId:id
}
})
},

toSeeMyCollection() {
var that = this;
var postData = {
uid: this.loginUid,
currentPage: 1
}
that.$http('get', that.$store.state.baseUrl + 'api/Circle/Collection/Page', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
for (var i in result) {
if (result[i].imgurls != '') {

if (result[i].imgurls.indexOf(',') == -1) {

var Arr = []
var obj = {
src: that.$store.state.imgUrl + result[i].imgurls
}
Arr.push(obj)
result[i].imgurls = Arr;
} else {
result[i].imgurls = result[i].imgurls.split(',')
var arr2 = []
for (var k in result[i].imgurls) {
var obj = {};
obj.src = that.$store.state.imgUrl + result[i].imgurls[k];
arr2.push(obj)
}
result[i].imgurls = arr2;
}
}
}
that.circleList = result;

}
})
},


toFellow(i) {
var that = this;
var postData = {
uid: this.uid,
followingUid: this.circleList[i].uid
}
that.$http('post', that.$store.state.baseUrl + 'api/UserFollowing', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.toSeeMyCollection()
}
})
this.circleList[i].follow = !this.circleList[i].follow;
}
}
}



