
import common from '@/assets/js/common'

import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
XButton,
Group,
PopupPicker,
Datetime,
AlertModule
},
data() {
return {
missionId: this.$route.query.id,
showData: {},
nickname: '',
stageDetail: {},
commentList: [],
nicknameArr: [],
missionCancelMsg:{},
nextStageTime:''
}
},

mounted() {

this.getMissiondetail(this.missionId);
this.getCommentList(this.missionId);
this.getStageDetail();


},
methods: {

cancelExecute() {
var that = this;
that.$http('post', that.$store.state.baseUrl + 'api/Task/Cancel', {
taskId: that.missionId,
uid: that.$store.state.uid
}).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: "取消成功"
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
},

getMissiondetail(id) {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/' + id + "?uid=" + that.$store.state.uid).then(function (res) {
if (res.data.code == '00') {

switch (res.data.data.status) {
case 1:
res.data.data.status = '发布中'
break;
case 2:
res.data.data.status = '已选定服务人'
break;
case 3:
res.data.data.status = '执行中'
break;
case 4:
res.data.data.status = '已结束'
break;
case 6:
res.data.data.status = '已取消'
break;
case 99:
res.data.data.status = '已失败'
break;
}
var result = res.data.data.infos;
for (var i in result) {
if (result[i].success) {
continue;
} else {
that.nextStageTime = result[i].date;
break;
}
}

that.nickname = res.data.data.nickname;
that.showData = res.data.data;
} else {
AlertModule.show({
title: res.data.msg
})
}

})
},

getConfirmWriter(id) {
var that = this;
var postData = {
taskId: id,
status: "2"
};
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
postData
)
.then(function (res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
var nicknameArr = [];
for (var i in result) {
nicknameArr.push(result[i].user.nickname);
}
that.confirmList = result;
that.nicknameArr = nicknameArr;
console.log("已确认列表");
console.log(nicknameArr);
}
});
},


getStageDetail() {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/Stage/' + that.missionId).then(function (res) {
if (res.data.code == '00') {
that.stageDetail = res.data.data;

}
})

},

getCommentList(id) {
var that = this;
that.$http('get', that.$store.state.baseUrl + "api/TaskComment/List?taskId=" + id).then(function (res) {
if (res.data.code == '00') {
var  result=res.data.data
for(var i in result){
result[i].commentTags= result[i].commentTags.split(',')
}
that.commentList = res.data.data
console.log(that.commentList)
}
})
},

giveRed(){
var that=this;
that.$router.push({
path:"/giveRedEnvelopes",
query:{
uid:that.showData.uid,
taskId:that.missionId
}
})
}
}
}



