
import common from "@/assets/js/common"
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
content: '',
tags: [],

}
},
components: {
XTextarea,
AlertModule
},
mounted() {
this.getTasgs()
},
methods: {
...common,

chooseTags(i) {
this.tags[i].active = true;

},
saveMyTags() {

var that = this;
var baseUrl = this.$store.state.baseUrl;
var arr = []
for (var i in this.tags) {
if (this.tags[i].active == true) {
arr.push(this.tags[i].tagName)
}
}
var postData = {
uid: this.$store.state.uid,
taskId: this.$route.query.taskId,
content: this.content,
commentTags: arr.join(',')
}
that.$http('post', baseUrl + 'api/TaskComment', postData).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: '提交成功',
onHide(){
that.$router.push({
path:'/missionInAfterEvaluteBuss',
query:{
keepActive:true,
id: that.$route.query.taskId
}
})
}
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
}
}
}



