
import wx from 'weixin-js-sdk';
import common from "@/assets/js/common"
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
uid: this.$store.state.uid,
userData: {},
money:''
}
},
mounted() {
this.getWebUser(this.uid);

this.wxConfig()
},
components:{
AlertModule
},
methods: {
...common,

tackAll(){
this.money=this.userData.amount
},

cashWithdrawal(){
var that = this;
var baseUrl = that.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/FeeRecord/Withdraw', {
uid: that.uid,
amount: that.money
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: "提现失败"
})
} else {


console.log('提现了')
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
package: result.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: result.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: result.paySign, // 支付签名
success: function (res) {
console.log('提现了'+res)

that.$router.push({
path:'/cashWithdrawalSuccess'
})
}
});
})
AlertModule.show({
title: "提现成功",
onHide(){
that.$router.push({
path:'/cashWithdrawalSuccess'
})
}
})
}

})
} ,

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://nian.im/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // 开启调试模式,
appId: result.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
timestamp: result.timestamp, // 必填，生成签名的时间戳
nonceStr: result.nonceStr, // 必填，生成签名的随机串
jsApiList: ["chooseWXPay"],
signature: result.signature, // 必填，签名，见附录1
jsToken: result.jsToken 
});
}
})
},
}

}



