
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
data() {
return {
uid: this.$store.state.uid,
nickname: '',
mobile: '',
content: ''
}

},
components: {
XTextarea,
AlertModule
},
methods: {
postFeedback() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
uid: this.uid,
nickname: this.nickname,
mobile: this.mobile,
content: this.content
}
that.$http('get', baseUrl + 'api/CommonProblem/List', postData).then(function (res) {
console.log("提交结果")
console.log(res.data)
if (res.data.code == '00') {
AlertModule.show({
title: '提交成功',
onHide(){
that.$router.go(-1)
}
})
}else{
AlertModule.show({
title: res.data.msg
})
}
})
}
}
}



