export default {
    mounted() {
        this.getSystemMsg()
    },
    data() {
        return {
            uid: this.$store.state.uid,
            msgList: []
        }
    },
    methods: {
        getSystemMsg() {
            // api/SystemMsg/List
            var that = this;
            var baseUrl = this.$store.state.baseUrl;
            that
                .$http("get", baseUrl + "api/SystemMsg/List", {
                    uid:that.uid
                })
                .then(function (res) {
                    console.log(res.data.data);
                    var result = res.data.data;
                    that.msgList = result;
                });
        }
    }
}



