
import common from "@/assets/js/common";
import appFooter from '@/components/footer/appFooter'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Badge from 'vux/src/components/badge/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

const pulldownDefaultConfig = {
content: "下拉刷新",
height: 40,
autoRefresh: false,
downContent: "下拉刷新",
upContent: "释放后刷新",
loadingContent: "正在刷新...",
clsPrefix: "xs-plugin-pulldown-"
};
const pullupDefaultConfig = {
content: "上拉加载更多",
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: "释放后加载",
upContent: "上拉加载更多",
loadingContent: "加载中...",
clsPrefix: "xs-plugin-pullup-"
};
export default {
components: {
Flexbox,
FlexboxItem,
AlertModule,
Badge,
Previewer,
TransferDom,
appFooter,
Scroller
},
directives: {
TransferDom
},
data() {
return {
nowPage: 1,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
postData: {},
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {
x: rect.left,
y: rect.top + pageYScroll,
w: rect.width
}


}
},
circleList: [],
dynamicData: '',
nowPath: '/circle',
uid: this.$store.state.uid
}
},
mounted() {
var that = this;
that.circleList=[]

that.$nextTick(() => {
that.$refs.scrollerBottom.reset({
top: 0
});
});
that.loadMore();



that.getCircleDynamic()
},
methods: {
...common,

refresh() {
var that = this;
if (that.nowPage != 0) {
that.nowPage--;
that.getCircleLoist(data => {
that.circleList = that.circleList.concat(data);
that.$refs.scrollerBottom.enablePullup();
that.$refs.scrollerBottom.donePulldown();
});
}
},
loadMore() {
var that = this;
that.getCircleLoist(data => {
if(data.length>0){
that.circleList = that.circleList.concat(data)
}
if (data.length >= 10) {
that.$refs.scrollerBottom.disablePullup();
}

that.$refs.scrollerBottom.donePullup();


});

that.nowPage++;

},


getCircleDynamic() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/Circle/Dynamic/Count', {
uid: that.uid
}).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.dynamicData = res.data.data

}

})
},

toCollection(id) {
var that = this;
var postData = {
uid: this.uid,
circleId: id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Collection', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.nowPage=1;
that.loadMore()

}
})
},

toLike(index) {
var that = this;
var postData = {
uid: this.uid,
circleId: that.circleList[index].id
}
that.$http('post', that.$store.state.baseUrl + 'api/Circle/Like', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

that.nowPage=1;
that.loadMore()
that.getCircleDynamic()
}
})
},

toComment(id) {
this.$router.push({
path: '/circleDetail',
query: {
circleId: id
}
})
},

toSeeMyCollection(fn) {
this.$router.push({
path: '/myCollectionCircle',



})
},

getCircleLoist(fn) {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.postData.currentPage = that.nowPage;
that.postData.loginUid = that.uid

that.$http('get', baseUrl + 'api/Circle/Page', that.postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
for (var i in result) {
if (result[i].imgurls != '') {

if (result[i].imgurls.indexOf(',') == -1) {

var Arr = []
var obj = {
src: that.$store.state.imgUrl + result[i].imgurls
}
Arr.push(obj)
result[i].imgurls = Arr;
} else {
result[i].imgurls = result[i].imgurls.split(',')
var arr2 = []
for (var k in result[i].imgurls) {
var obj = {};
obj.src = that.$store.state.imgUrl + result[i].imgurls[k];
arr2.push(obj)
}
result[i].imgurls = arr2;
}
}
}
fn(res.data.data)
}
console.log('圈子列表')
console.log(result)

})
},

toFellow(i) {
var that = this;
var postData = {
uid: this.uid,
followingUid: this.circleList[i].uid
}
that.$http('post', that.$store.state.baseUrl + 'api/UserFollowing', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.nowPage=1;

that.loadMore()
}
})
this.circleList[i].follow = !this.circleList[i].follow;
},

show(index,j) {
console.log('---'+index)
console.log(this.$refs.previewer)
this.$refs.previewer[index].show(j)
},
}
}



