
import common from "@/assets/js/common";
import slidePicker from "@/components/slidePicker";
import writers from "@/components/writer/writer";
import radioPicker from "@/components/radioPicker";
import sliderPopupPicker from "@/components/sliderPopupPicker";
import sortPopup from "@/components/sortPopup/sortPopup";
import AlertModule from 'vux/src/plugins/alert/module'
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
data() {
return {
id: this.$route.query.taskId,
isCanChoose: true,
enrollerWriterListA: [],
enrollerWriterListB: [],
chooseNum: 0,
chooseWriterId: "",
pxIsShow: false,
sxIsShow: false,
allWriter: '',
allPeople: '',
rightText1: "",
rightText2: "",
stringValue1: false,
stringValue2: false,
stringValue3: false,
radioTitle: "选择性别",
radios_sex_Arr: [{
value: 0,
text: "不限",
selected: true
},
{
value: 1,
text: "男",
selected: false
},
{
value: 2,
text: "女",
selected: false
}
],
minWorkAge: "",
maxWorkAge: "",
sortOptions: [{
title: "优先显示我已经加了关注的用户",
value: 'follow'
},
{
title: "优先显示相关用户数最多的用户",
value: 'userCount'
},
{
title: "优先显示写作小时数最多的用户",
value: 'workHours'
},
{
title: "优先显示每人小时费最少的用户",
value: 'price'
},
{
title: "优先显示累计写作值最多的用户",
value: 'work'
}

],
choosBussId: "",
choosBuss: {},
chooseBusNum: 0,
chooseBussId: "",
writerShow: true,
bussShow: false,
mainShow: true,

taskType: this.$route.query.taskType,

priceType: this.$route.query.priceType,
choosePopel: "作者",

busniessCount: this.$route.query.busniessCount,
postData: {},
order: "",
sliderClassArr: [],
sliderSubjectArr: [],
sliderBigClassArr: [],
subjectArr: [],
classShow: false,
categoryShow: false,
gradesArr: [],
leftText1: "年级",
leftText2: "科目",

};
},
watch: {
order: {
handler(newName, oldName) {
this.getEnrollWriter(this.id, newName);
},
deep: true,
immediate: true
}
},
mounted() {
this.getCategory();
this.getGrade();
this.getEnrollWriter(this.id);

this.getBigGrade();

if (this.taskType == "2") {
this.choosePopel = "书商";
this.writerShow = false;
this.bussShow = true;
}
},
components: {
Popup,
sortPopup,
writers,
AlertModule,
radioPicker,
sliderPopupPicker,
sortPopup,
Cell,
Group,
XSwitch,
XButton
},
methods: {
...common,

submitId(id) {
var that = this;
var result;
var postData = {
id: id,
status: "2"
};
that.$http("post",
that.$store.state.baseUrl + "api/Task/Apply/Select",
postData
)
.then(function (res) {
if (res.data.code != "00") {



} else {}
});
return result;
},
getResult() {

var arr = "";
for (let i in this.sliderBigClassArr[0]) {
if (this.sliderBigClassArr[0][i].active == true) {
arr = this.sliderBigClassArr[0][i].name;
}
}

this.mainShow = true;
this.classShow = false;
this.rightText1 = arr;
},
getResult2() {

var arr = "";
for (let i in this.subjectArr) {
if (this.subjectArr[i].active == true) {
arr = this.subjectArr[i].categroy;
this.subject = this.subjectArr[i].id;
}
}
this.mainShow = true;
this.categoryShow = false;
this.rightText2 = arr;
},
gotoMultiPicker(val) {
if (val == "class") {

this.classShow = true;
(this.categoryShow = false),
(this.coordinationShow = false),
(this.mainShow = false);
} else {
(this.classShow = false),
(this.coordinationShow = false),
(this.categoryShow = true);
this.mainShow = false;
}
},

confirmWriter() {
var that = this;
if (that.taskType == "1") { // 找作者(只有独资方式，所以只有一个作者)
if (that.chooseWriterId == "") {
AlertModule.show({
title: "您还未选择作者"
});
} else { // 选择了作者了
var chooseWriter = []
for (var k in that.enrollerWriterListA) {

if (that.enrollerWriterListA[k].isChoosen = true) {
chooseWriter.push(that.enrollerWriterListA[k].uid);
}
that.submitId(that.enrollerWriterListA[k].id)
}
that.$router.push({
path: "/replyDetails",
query: {
isNew: true,
taskId: that.id,
chooseWriterId: chooseWriter.join(','),
sendSuccess: false
}
})
}
} else if (that.taskType == "2") { // 找书商
if (that.priceType == "1") { // 找书商独资
if (that.chooseBusNum > 1 || that.chooseBusNum == 0) {
AlertModule.show({
title: "独资任务，请选择一个书商"
});
} else {
var chooseWriter = []
for (var k in that.enrollerWriterListB) {
if (that.enrollerWriterListB[k].isChoosen = true) {
chooseWriter.push(that.enrollerWriterListB[k].uid);
}
that.submitId(that.enrollerWriterListB[k].id)
}
that.$router.push({
path: "/replyDetails",
query: {
isNew: true,
taskId: that.id,
chooseWriterId: chooseWriter.join(','),
sendSuccess: false
}
})
}
} else {
if (that.chooseBusNum == 0) { // 找书商合资
AlertModule.show({
title: "请选择书商"
});
} else if (that.chooseBusNum != that.busniessCount) {
AlertModule.show({
title: "请选择" + that.busniessCount + "个书商"
});
} else if (that.chooseBusNum == that.busniessCount) {
var chooseWriter = []
for (var k in that.enrollerWriterListB) {
if (that.enrollerWriterListB[k].isChoosen == true) {
chooseWriter.push(that.enrollerWriterListB[k].uid);
}
that.submitId(that.enrollerWriterListB[k].id)
}
that.$router.push({
path: "/replyDetails",
query: {
isNew: true,
taskId: that.id,
chooseWriterId: chooseWriter.join(','),
sendSuccess: false
}
})
}
}
} else if (that.taskType == "3") {


if (that.chooseWriterId == "") {
AlertModule.show({
title: "您还未选择作者"
});
} else {
this.choosePopel = "书商";
this.writerShow = false;
this.bussShow = true;

if (that.chooseBusNum == 0) {
AlertModule.show({
title: "请选择书商"
});
} else if (that.chooseBusNum != that.busniessCount) {
AlertModule.show({
title: "请选择" + that.busniessCount + "个书商"
});
} else if (that.chooseBusNum == that.busniessCount) {



var allId = []; 
var allUid = []
for (var k in that.enrollerWriterListA) {
if ((that.enrollerWriterListA[k].isChoosen = true)) {
allId.push(that.enrollerWriterListA[k].id);
allUid.push(that.enrollerWriterListA[k].uid)
}
}
for (var k in that.enrollerWriterListB) {
if ((that.enrollerWriterListB[k].isChoosen = true)) {
allId.push(that.enrollerWriterListB[k].id);
allUid.push(that.enrollerWriterListB[k].uid)
}
}

for (var i in allId) {
that.submitId(allId[i]);
}
that.$router.push({
path: "/replyDetails",
query: {
isNew: true,
taskId: that.id,
chooseWriterId: allUid.join(','),
sendSuccess: false
}
})

}
}
}

},

chooseWriter(i) {
var that = this;
that.chooseNum = 1;
for (var k in that.enrollerWriterListA) {
that.enrollerWriterListA[k].isChoosen = false;
}
that.enrollerWriterListA[i].isChoosen = true;
that.chooseWriterId = that.enrollerWriterListA[i].id;
},

chooseBuss(i) {
var that = this;
that.enrollerWriterListB[i].isChoosen = !that.enrollerWriterListB[i]
.isChoosen;
var arr = [];
for (var k in that.enrollerWriterListB) {
if (that.enrollerWriterListB[k].isChoosen == true) {
arr.push(that.enrollerWriterListB[k].isChoosen);
}
}
that.chooseBusNum = arr.length;

},

addActive(index) {
for (var i in this.sliderBigClassArr[0]) {
this.sliderBigClassArr[0][i].active = false;
}
this.sliderBigClassArr[0][index].active = true;
},
addActive2(index) {

for (var i in this.subjectArr) {
this.subjectArr[i].active = false;
}
this.subjectArr[index].active = true;
},
changeResult(val) {},
changeResultGrade(val) {

this.postData.classNo = val;
},
changeResultKM(val) {

this.postData.category = val;
this.categoryName = val.name;
},

showSSResult() {

console.log("筛选条件");

var that = this;
that.page = '1'
that.postData = {
order: that.order,
taskId: that.id,
status: that.$route.enrollStatus,

classNo: that.rightText1,
subject: that.rightText2,
realAuth: that.stringValue1 ? true : '',
eduAuth: that.stringValue2 ? true : '',
authorAuth: that.stringValue3 ? true : '',
gender: that.chooseSex,
minWorkAge: that.minWorkAge,
maxWorkAge: that.maxWorkAge,
currentPage: that.page,

};

that.sxIsShow = false;
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
that.postData
)
.then(function (res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
var authorArr = [];
var bussArr = [];
for (var i in result) {
result[i].isChoosen = false;
result[i].userTags=result[i].user.authorInfo.userTags;
if (result[i].userType == "author") {
authorArr.push(result[i]);
} else {
bussArr.push(result[i]);
}
}
that.enrollerWriterListA = authorArr;
console.log("书商的列表");
console.log(bussArr);
that.enrollerWriterListB = bussArr;
}
});

},


getEnrollWriter(id, order) {
var that = this;




that.postData.order = order;
that.postData.taskId = id;
that.postData.status = that.$route.enrollStatus;

that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
that.postData
)
.then(function (res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
that.allWriter = res.data.data.length;
var authorArr = [];
var bussArr = [];
for (var i in result) {

if(result[i].user.authorInfo.userTags.length>2){
result[i].user.authorInfo.userTags=result[i].user.authorInfo.userTags.slice(0,2)
}
result[i].isChoosen = false;
if (result[i].userType == "author") {
authorArr.push(result[i]);
} else {
bussArr.push(result[i]);
}
}
that.enrollerWriterListA = authorArr;
console.log("书商的列表");
console.log(bussArr);
that.enrollerWriterListB = bussArr;
}
});
}
}
};



