
import appFooter from '@/components/footer/appFooter'
import sliderPopupPicker from '@/components/sliderPopupPicker'
import radioPicker from '@/components/radioPicker'
import slidePicker from '@/components/slidePicker'
import common from '@/assets/js/common'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'




export default {
components: {
radioPicker,
slidePicker,
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider,
Checker,
CheckerItem,
Flexbox,
FlexboxItem,
CheckIcon,
Group,
appFooter,
sliderPopupPicker,
XAddress
},
mounted() {
this.myarea=[];
this.getCategory();
this.getBigGrade();
this.getPriceArr();

this.getSwipper();
console.log('用户'+this.$store.state.uid)
},
methods: {
...common,

getSwipper() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http("get", baseUrl + "api/Banner/List").then(function (res) {
if (res.data.code == "00") {
var result = res.data.data;
for (var i in result) {
result[i].img = that.$store.state.imgUrl+result[i].bannerSrc;
}
that.swipperList = result
}
});
},
changeResultGrade(val){
console.log('首页年纪'+val)
this.postData.classNo=val.name;
},

onAddressChange(val) {
var arr = [];
console.log(value2name(this.myarea, ChinaAddressV4Data))
this.postData.area = value2name(this.myarea, ChinaAddressV4Data).split(' ').join(",");
},
gotoSeeResult() {

if(this.postData.priceType==undefined){
this.postData.priceType=0;
}

switch(this.postData.coordination){
case undefined:
this.postData.coordination='';
break;
case 'author':
this.postData.coordination='作者上门';
break;
case 'online':
this.postData.coordination='远程协作';
break;
case 'busniess':
this.postData.coordination='书商拜访';
break;
}



delete this.postData.category;

console.log(this.postData)
this.$router.push({
path: '/searchResult',
query: {
postData:JSON.stringify( this.postData)
},
meta:{
keepAlive: false
}
})
},
onSwiperItemIndexChange(index) {
console.log('demo item change', index)
},
demo01_onIndexChange(index) {
this.demo01_index = index
},

changeGrade(val) {
this.tempVla = val[0] + '/';
},

hide: function () {
this.tempvalue = this.tempvalue.concat(this.tempVla);

this.value2 = this.tempvalue;
},
format1: function (value, name) {
return `${value[0]}/${value[1]}/${value[2]}`
},
toCheck(val) {
console.log(val)
if (val == 1) {
this.checkFlag1 = true;
this.checkFlag2 = false;
} else {
this.checkFlag2 = true;
this.checkFlag1 = false;
}
}
},
data() {
return {

nowPath: '/index',
demo01_index: 0,
classNo: '',
postData: {},
category: '',
subject:'',
area: '',
myarea: [],
priceArr: [],

title1: '年级',
title2: '科目',
title3: '价格区间',
title4: '协作方式',
title5: '协作区域',
addressData: ChinaAddressV4Data,

slidePriceArr: [],
sliderSubjectArr: [],
sliderBigClassArr: [],
radioTitle: '选择性别',
publish_title: '出版方式',
slidePriceArr2: [],
coordination: [
['不限', '作者拜访', '书商拜访', '远程协作']
],
leftText2: '科目',
leftText3: '价格区间',
leftText4: '协作方式',
leftText5: '协作区域',
leftText1: '年级',
rightText1: '不限',
radiosArr: [
{
value: 0,
text: '不限',
selected: true
},{
value: 1,
text: '独资',
selected: false
},
{
value: 2,
text: '合资',
selected: false
},
],

checkType: '',
checkFlag1: false,
checkFlag2: false,
swipperList: []

}
}
}



