
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import sliderPopupPicker from '@/components/sliderPopupPicker'
export default {
data() {
return {
year: '',
yearsArr: [],
value1: '',
value2: '',
value3: '',
value4: '',
uid: this.$store.state.uid,
isios: false,
frontUrl: '',
backUrl: '',
rightText:''
}
},
components: {
XTextarea,
Group,
AlertModule,
sliderPopupPicker
},
mounted() {

var ua = navigator.userAgent.toLowerCase();

var isIos = ua.indexOf("iphone") != -1 || ua.indexOf("ipad") != -1;
if (isIos) {
this.isios = true;
} else {
this.isios = false;
}
var arr = []
for (let i = 0; i <= 100; i++) {
arr.push(i)
}
this.yearsArr.push(arr)
},
methods: {
frontImg() {
var file = document.getElementById("upload_file1");
file.click()
},
backImg() {
var file1 = document.getElementById("upload_file2");
file1.click()
},
changeResultEdu(val) {
this.degree = val.value
this.rightText = val.value
},

chooseImageFront() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file1").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
};

axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var showUrl = 'http://nian.im/storage/' + res.data.data
that.frontUrl = showUrl

}
})
},


chooseImageBack() {
var that = this;
var baseUrl = "https://nian.im/works/"
var file = document.getElementById("upload_file2").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头

axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var showUrl = 'http://nian.im/storage/' + res.data.data
that.backUrl = showUrl

}
})
},
changeResultGrade(val) {
console.log(val)
this.rightText=val.value;
this.year = val.value;
},
submitData() {
var that = this;
var data = {
achievement: that.value1,
books: that.value3,
experience: that.value2,
material: that.frontUrl,
org: that.value4,
workAge: that.year
}
var flag=true;
for(var i in data){
if(!Boolean(data[i])){
AlertModule.show({
title:"请填写全部信息"
})
flag=false
break;
}
continue;
}
if(flag){
data.othersMaterial=that.backUrl;
data.uid= that.$store.state.uid
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/WebUser/Certificate/Author', data).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: '保存成功！',
onHide(){
that.$router.go(-1)
}
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
}

}
}
};



