
import appFooter from "@/components/footer/appFooter";
import common from "@/assets/js/common";
import slidePicker from "@/components/slidePicker";
import writers from "@/components/writer/writer";
import radioPicker from "@/components/radioPicker";
import sliderPopupPicker from "@/components/sliderPopupPicker";
import sortPopup from "@/components/sortPopup/sortPopup";
import Range from 'vux/src/components/range/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

const pulldownDefaultConfig = {
content: "下拉刷新",
height: 40,
autoRefresh: false,
downContent: "下拉刷新",
upContent: "释放后刷新",
loadingContent: "正在刷新...",
clsPrefix: "xs-plugin-pulldown-"
};
const pullupDefaultConfig = {
content: "上拉加载更多",
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: "释放后加载",
upContent: "上拉加载更多",
loadingContent: "加载中...",
clsPrefix: "xs-plugin-pullup-"
};
export default {

components: {
appFooter,
sortPopup,
Range,
writers,
Popup,
XSwitch,
Cell,
XButton,
Radio,
Checker,
CheckerItem,
Flexbox,
FlexboxItem,
CheckIcon,
GroupTitle,
Group,
Picker,
sliderPopupPicker,
radioPicker,
PopupPicker,
Scroller
},
mounted() {
this.rangValue = 0;

this.getCategory();
this.getGrade();
this.postData = JSON.parse(this.$route.query.postData);
console.log("搜索参数");
console.log(this.postData);

this.searchResultList(this.page);

this.getBigGrade();



this.$nextTick(() => {
this.$refs.scrollerBottom.reset({
top: 0
});
});
this.loadMore();

},

data() {
return {
sortOptions: [{
title: "优先显示我已经加了关注的用户",
value: 'follow'
},
{
title: "优先显示相关用户数最多的用户",
value: 'userCount'
},
{
title: "优先显示写作小时数最多的用户",
value: 'workHours'
},
{
title: "优先显示每人小时费最少的用户",
value: 'price'
},
{
title: "优先显示累计写作值最多的用户",
value: 'work'
}

],
nowPath: "/searchResult",
classShow: false,
mainShow: true,
categoryShow: false,
sliderClassArr: [],
sliderSubjectArr: [],

gradesArr: [],
leftText1: "年级",
leftText2: "科目",

rightText1: "",
subjectArr: [],
leftText2: "科目",
rightText2: "",

minWorkAge: "",
maxWorkAge: "",
radioTitle: "选择性别",
radios_sex_Arr: [{
value: 0,
text: "不限",
selected: true
},
{
value: 1,
text: "男",
selected: false
},
{
value: 2,
text: "女",
selected: false
}
],
chooseSex: "0",

min: 0,
max: 80,
step: 1,
pxIsShow: false,
sxIsShow: false,
stringValue1: false,
stringValue2: false,
stringValue3: false,
rangValue: 0,

order: "",
postData: this.$route.query.postData,
writerList: [],
sliderBigClassArr: [],
noDataText: "没有数据了",
page: 1,
allPeople: 0,
allWriter: "",
isBottom: false,
categoryName: "",
list: [],
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig
};
},
watch: {
order: {
handler(newName, oldName) {

this.postData.order=newName
this.page=1;
this.searchResultList(this.page, newName);

},
deep: true,
immediate: true
}
},
methods: {
...common,


showSSResult() {

console.log("筛选条件");

var that = this;
that.page = '1'
that.postData = {
classNo: that.rightText1,
subject: that.rightText2,
realAuth: that.stringValue1 ? true : '',
eduAuth: that.stringValue2 ? true : '',
authorAuth: that.stringValue3 ? true : '',
gender: that.chooseSex,
minWorkAge: that.minWorkAge,
maxWorkAge: that.maxWorkAge,
currentPage: that.page,

};

that.sxIsShow = false;
that
.$http(
"get",
that.$store.state.baseUrl + "api/WebUser/Query/Author",
that.postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {

that.writerList = [];
that.loadMore();

}
});
},


getTotalWriter() {

var that = this;
var baseUrl = this.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/WebUser/AuthorTotal")
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.allWriter = res.data.data;

}
});
},


refresh() {
var that = this;
if (that.page != 0) {
that.page--;
that.searchResultList(data => {
that.writerList = data;
that.$refs.scrollerBottom.enablePullup();
that.$refs.scrollerBottom.donePulldown();
});
}
},
loadMore() {
var that = this;
that.searchResultList(data => {
if (that.writerList.length >= 10) {
that.$refs.scrollerBottom.disablePullup();
}
that.writerList = that.writerList.concat(data);
that.$refs.scrollerBottom.donePullup();
});
that.page++;

},



searchResultList(fn) {
var that = this;

this.loading = false;


that.postData.currentPage = that.page;

that.getNameById(that.postData.classNo, that.sliderBigClassArr);
that
.$http(
"get",
that.$store.state.baseUrl + "api/WebUser/Query/Author",
that.postData
)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
that.allPeople = res.data.pi.totalSize;

if (result.length != 0) {
fn(result);
}

}
});
},
getResult() {

var arr = "";
for (let i in this.sliderBigClassArr[0]) {
if (this.sliderBigClassArr[0][i].active == true) {
arr = this.sliderBigClassArr[0][i].name;

}
}

this.mainShow = true;
this.classShow = false;
this.rightText1 = arr;
},
getResult2() {

var arr = "";
for (let i in this.subjectArr) {
if (this.subjectArr[i].active == true) {
arr = this.subjectArr[i].categroy;
this.subject = this.subjectArr[i].id;
}
}
this.mainShow = true;
this.categoryShow = false;
this.rightText2 = arr;
},
gotoMultiPicker(val) {
if (val == "class") {

this.classShow = true;
(this.categoryShow = false),
(this.coordinationShow = false),
(this.mainShow = false);
} else {
(this.classShow = false),
(this.coordinationShow = false),
(this.categoryShow = true);
this.mainShow = false;
}
},

addActive(index) {
for (var i in this.sliderBigClassArr[0]) {
this.sliderBigClassArr[0][i].active = false;
}
this.sliderBigClassArr[0][index].active = true;
},
addActive2(index) {

for (var i in this.subjectArr) {
this.subjectArr[i].active = false;
}
this.subjectArr[index].active = true;
},
changeResult(val) {},
changeResultGrade(val) {

this.postData.classNo = val;
},
changeResultKM(val) {

this.postData.subject = val;
this.categoryName = val.name;
}
},

directives: {
TransferDom
}
};



