
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
isSaw: false,
mailId: this.$route.query.mailId,
mailDetail: {},
comments: [],
msgId:'',
content:''
}
},
components: {
AlertModule
},
mounted() {
this.getDetail()
},
methods: {
getDetail() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + '/api/UserMsg/' + this.mailId).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.mailDetail = res.data.data;
that.comments = res.data.data.comments
}
})
},

replayMsgId(id){
this.$refs.input.focus()
this.msgId=id;
},
sendMsg() {
if (event.keyCode == 13) {

event.preventDefault(); 
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/UserMsg/Comments', {
msgId:that.msgId,
uid:that.$store.state.uid,
content:that.content
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.getDetail();
that.content=''
}
})
}
}
}
}



