
import common from "@/assets/js/common"

import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
uid: this.$store.state.uid,
userData: {},
feeRecordList: []
}
},
components:{
AlertModule
},
mounted() {
this.getWebUser(this.uid);
this.getFeeRecore();


},
methods: {
...common,


getFeeRecore() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/FeeRecord/List?' + that.uid).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.feeRecordList = res.data.data;
}

})
},

toRecharge() {
this.$router.push({
path: '/recharge'
})
},

cashWithdrawal() {
this.$router.push({
path: '/cashWithdrawal'
})
}
}
};



