
import common from '@/assets/js/common'

import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
XButton,
Group,
PopupPicker,
Datetime,
AlertModule
},
data() {
return {
missionId: this.$route.query.id,

nextStageTime: "",

remainingTime: {},
showData: {},
nickname: '',
stageDetail: {},
nicknameArr: [],
confirmList: [],
missionCancelMsg: {},
isCanRegister: false,
isPay: false

}
},

mounted() {
this.getMissiondetail(this.missionId);
this.getStageDetail()
this.getConfirmWriter(this.missionId)
this.getCancelMsg();

},
methods: {

getConfirmWriter(id) {
var that = this;
var postData = {
taskId: id,
status: "2"
};
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
postData
)
.then(function (res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
var nicknameArr = [];
for (var i in result) {
nicknameArr.push(result[i].user.nickname);
}
that.confirmList = result;
that.nicknameArr = nicknameArr;
console.log("已确认列表");
console.log(nicknameArr);
}
});
},

getMissiondetail(id) {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/' + id + "?uid=" + this.$store.state.uid).then(function (res) {
if (res.data.code == '00') {
var obj = res.data.data.webUser;
if (res.data.data.payTime == null) {
that.isPay = false;
} else {
isPay = true
}
that.nickname = obj.nickname;

switch (res.data.data.status) {
case 1:
res.data.data.status = '发布中'
break;
case 2:
res.data.data.status = '已选定服务人'
break;
case 3:
res.data.data.status = '执行中'
break;
case 4:
res.data.data.status = '已结束'
break;
case 6:
res.data.data.status = '已取消'
break;
case 99:
res.data.data.status = '已失败'
break;
}
that.showData = res.data.data;
} else {
AlertModule.show({
title: res.data.msg
})
}

})
},

agreeCancel(num) {
var that = this;
var postData = {
status: num
}

var url = that.$store.state.baseUrl + 'api/Task/Cancel/' + that.missionCancelMsg.id;

that.$http('put', url, postData).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: "操作成功"
})
} else {
AlertModule.show({
title: "操作失败"
})
}
})
},

cancelExecute() {
var that = this;
that.$http('post', that.$store.state.baseUrl + 'api/Task/Cancel', {
taskId: that.missionId,
uid: that.$store.state.uid
}).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: "取消成功"
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
},

getCancelMsg() {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/CancelTask', {
taskId: that.missionId
}).then(function (res) {
if (res.data.code == '00') {
that.missionCancelMsg = res.data.data;
var time = res.data.data.applyTime;
var time3;
time3 = new Date(time.replace(/-/g, "/")).getTime() + 1000 * 60 * 60 * 24 * 3;
time3 = that.format(time3)
var end = that.format(new Date())
var day = that.GetDateDiff(end, time3, "day")
var hour = Number(that.GetDateDiff(end, time3, "hour")) - day * 24
var minute = that.GetDateDiff(end, time3, "minute") - (day * 24 * 60 + hour * 60)
console.log('时间' + day + "-" + hour + "-" + minute)
that.remainingTime = {
day: day,
hour: hour,
minute: minute
};

console.log(res.data.data)
} else {
AlertModule.show({
title: res.data.msg
})
}
})
},

GetDateDiff(startTime, endTime, diffType) {

startTime = startTime.replace(/\-/g, "/");
endTime = endTime.replace(/\-/g, "/");


diffType = diffType.toLowerCase();
var sTime = new Date(startTime); 
var eTime = new Date(endTime); 

var divNum = 1;
switch (diffType) {
case "second":
divNum = 1000;
break;
case "minute":
divNum = 1000 * 60;
break;
case "hour":
divNum = 1000 * 3600;
break;
case "day":
divNum = 1000 * 3600 * 24;
break;
default:
break;
}
return parseInt((eTime.getTime() - sTime.getTime()) / parseInt(divNum));
},

add0(m) {
return m < 10 ? '0' + m : m
},

format(shijianchuo) {
var that = this;

var time = new Date(shijianchuo);
var y = time.getFullYear();
var m = time.getMonth() + 1;
var d = time.getDate();
var h = time.getHours();
var mm = time.getMinutes();
var s = time.getSeconds();
return y + '-' + that.add0(m) + '-' + that.add0(d) + ' ' + that.add0(h) + ':' + that.add0(mm) + ':' + that.add0(s);
},


getStageDetail() {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/Stage/' + that.missionId).then(function (res) {
if (res.data.code == '00') {

var result = res.data.data.infos;
for (var i in result) {
if (result[i].success) {
continue;
} else {
that.nextStageTime = result[i].date;
break;
}
}


if (new Date().getTime() >= new Date(that.nextStageTime).getTime()) {
that.isCanRegister = true;
}
that.stageDetail = res.data.data;

}
})

},

gotoWrite() {
var that = this;
if (that.isPay) {
that.$router.push({
path: 'stagePlan',
query: {
missionId: that.missionId,
stageDetail: JSON.stringify(that.stageDetail)
}
})
} else {
AlertModule.show({
title: "对方尚未支付"
})
}

},

gotoRegist() {
var that = this;
that.$router.push({
path: '/registAchievement',
query: {
missionId: that.missionId,
stageDetail: JSON.stringify(that.stageDetail)
}
})
}

}
}



