
import common from '@/assets/js/common'
import writers from '@/components/writer/writer'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHr from 'vux/src/components/x-hr/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Badge,
writers,
Step,
StepItem,
XButton,
XHr,
AlertModule,
Flexbox,
FlexboxItem,
Toast
},
data() {
return {
isCanChoose: true,

remainingTime: {},
step1: 1,
step2: 0,

id: this.$route.query.id,
showData: {},
shortClass: '',
shortSubject: '',

apply: false,
enrollerWriterList: [],
enrollerBussList: [],

isMyTask: this.$route.query.isMyTask,
authorInfo: {},
userData:{},
taskStatus:'',
taskType:'1',
enrollStatus:0,
isPay:false,
amount:0
}
},
mounted() {

this.getMissiondetail(this.id);


this.getWebUser(this.$store.state.uid)




},
methods: {
...common,

toConfirmTask(){
var that=this;
that.$router.push({
path:'/toConfirmMission',
query:{
chooseWriterId:that.$store.state.uid,
isNew:false,
taskId:that.id,
sendSuccess:true
}
})
},

chooseWriter(i) {
console.log('点击了')
var that = this;
for (var i in that.enrollerWriterList) {
that.enrollerWriterList[i].isChoosen = false;
}
that.enrollerWriterList[i].isChoosen = !that.enrollerWriterList[i].isChoosen;
},

deleteTesk() {
var that = this;
that.$http('delete', that.$store.state.baseUrl + 'api/Task/' + that.id).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "取消成功"
});
that.$router.go(-1)
}
})
},

getEnrollWriter(status) {
var that = this;


if(status==2){
that.enrollStatus=1
}else if(status==7){
that.enrollStatus=1
}
console.log('@@@@@@@@@@@@@'+status)
var postData = {

taskId: that.id,
status:  that.enrollStatus

}
that.$http('get', that.$store.state.baseUrl + 'api/Task/Apply/List', postData).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
var arr=[]
var arr1=[]
for (var i in result) {
if(result[i].userType=='author'){
arr.push(result[i])
}else{
arr1.push(result[i])
}
result[i].isChoosen = false
}

that.enrollerBussList = arr1;
that.enrollerWriterList = arr;
console.log('获取报名的作者列表')
console.log(that.enrollerBussList)
}
})
},

toEnroll() {
var that = this;

var missionType=that.showData.taskType;

var userType=that.userData.type;
if(userType=='author'&&missionType=='2'||userType=='business'&&missionType=='1'){
AlertModule.show({
title: "报名身份条件不满足",
})
}else{
var postData = {
taskId: that.id,
uid: this.$store.state.uid
}
that.$http('post', that.$store.state.baseUrl + 'api/Task/Apply', postData).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg,
onHide: function () {
that.$router.push({
name: 'mission'
})
}
})
} else {
AlertModule.show({
title: '报名成功',
onHide: function () {
that.$router.push({
name: 'mission'
})
}

})

}
})
}
},

getMissiondetail(id) {
var that = this;
that.$http('get', that.$store.state.baseUrl + 'api/Task/' + id + "?uid=" + this.$store.state.uid).then(function (res) {
that.apply = res.data.data.apply
that.taskType=res.data.data.taskType
that.taskStatus=res.data.data.status;
that.amount=res.data.data.amount
if(res.data.data.payTime==null){
that.isPay=true
}else{
that.isPay=false
}
that.getEnrollWriter(res.data.data.status)

switch (res.data.data.status) {
case 1:
res.data.data.status = '发布中'
break;
case 2:
res.data.data.status = '已确认'
break;
case 3:
res.data.data.status = '执行中'
break;
case 7:
res.data.data.status = '待确认'
break;
case 4:
res.data.data.status = '已结束'
break;
case 6:
res.data.data.status = '已取消'
break;
case 99:
res.data.data.status = '已失败'
break;
}

that.authorInfo = res.data.data.webUser.authorInfo;

that.showData = res.data.data;




console.log(that.apply+"---"+that.taskStatus)
})
},
nextStep() {
this.step2++
}
}
}



